<template>
  <div>
    <div class="listTitle">
        <i></i>
        <span></span>
        <!-- <el-button
          class="fr"
          type="primary"
          size="small"
          icon="el-icon-plus"
          @click="handleEdit(scope.$index, scope.row)"
        >
          
        </el-button> -->
    </div>

    <el-table :data="tableData" @row-dblclick="handleRowDblClick" border class="mb10" style="text-align:center" height="600" @selection-change="handleSelectionChange">
        <el-table-column fixed="left" type="selection"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="caseNo" label=""></el-table-column>
        <el-table-column prop="type" label="">
          <template slot-scope="scope">
            <span>{{scope.row.type === 'PT' ? '': '' }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="phone" label=""></el-table-column>
        <el-table-column prop="age" label=""></el-table-column>
        <el-table-column prop="sex" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.sex === 'MALE'"></span>
            <span v-if="scope.row.sex === 'FEMALE'"></span>
            <span v-if="scope.row.sex === 'UNKNOWN'"></span>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" label=""></el-table-column>
        <el-table-column prop="firstMemberName" label=""></el-table-column>
        <el-table-column prop="source1" label=""></el-table-column>
        <el-table-column prop="source2" label=""></el-table-column>
        <el-table-column prop="source3" label=""></el-table-column>
        <el-table-column label="" width="240" fixed="right">
          <slot :row="scope.row" slot-scope="scope"></slot>
        </el-table-column>
      </el-table>
  </div>
</template>

<script>
export default {
  components:{},
  props:{
    tableData: {
      type: Array,
      default: []
    },
    tabConKeyName: {
      type: String,
      default: ''
    }
  },
  data(){
    return {
    }
  },
  watch:{},
  computed:{},
  methods:{
    handleSelectionChange() {
      this.$emit('handleSelectionChange')
    },
    handleRowDblClick(val){
      this.$emit('handleRowDblClick',val)
    }
  },
  created(){},
  mounted(){}
}
</script>
<style lang="less" scoped>
</style>


